

























import {
  Board
  pos_from_xy
  pos_to_str
  pos_from_str
  pos_array_from_str
  BLACK
  WHITE
} from '@oth/board'
import Disc from './Disc'
import Player from '../player.worker'
import i18n from '../i18n'
import sound from '../sound'

worker = new Player

export default
  props: ['user', 'level', 'guide', 'message', 'back', 'set_undo_btn',
    'entered_moves']

  data: -> {
    board: new Board
    turn: BLACK
    flips: []
    hover_at: null
    undo_stack: []
    thinking: false
    user_moves: 0
    gameover: false
    will_flip_enabled: false
    keys: []
    BLACK
    i18n
  }

  mounted: ->
    worker.postMessage type:'set_level', level:@level
    if @turn == @user
      @message text: @i18n.t.your_turn
    else
      @worker_move()
    gtag 'event', 'start',
      event_category: 'game'
      event_label: @level

    @key_listener = (e) => @keypress(e)
    window.addEventListener 'keypress', @key_listener

  beforeDestroy: ->
    window.removeEventListener 'keypress', @key_listener

  updated: -> @$el.classList.add 'animate'

  computed:
    can_undo: -> not @thinking and @user_moves and not @gameover

    rows: ->
      rows = for y in [0...8]
        row = []
        [0...8].forEach (x) =>
          pos = pos_from_xy x, y
          disc = @board.get(pos)

          row.push
            disc: if disc == BLACK
                    'black'
                  else if disc == WHITE
                    'white'
                  else
                    null
            can_move:
              if @turn == @user
                @board.can_move(@turn, pos)
              else
                false
            is_hover:
              if @turn == @user
                @hover_at == pos and @board.can_move(@turn, pos)
              else
                false
            will_flip: @will_flip_enabled and @flips.indexOf(pos) >= 0
            move: =>
              if @turn == @user
                @move pos
            enter: =>
              if @turn == @user
                @flips = @board.move(@turn, pos)
                if @flips.length
                  @board.undo @turn, pos, @flips
                  @hover_at = pos
                @will_flip_enabled = false
            leave: =>
              @hover_at = null
              @flips = []
              @will_flip_enabled = false
        row

      setTimeout (=> @will_flip_enabled = true), 50 # XXX

      rows

  methods:
    move: (pos) ->
      @flips = []
      @hover_at = null
      flips = @board.move @turn, pos
      return unless flips.length
      sound 'move'
      @$emit 'add-move', pos_to_str(pos, @turn)
      @undo_stack.push [@turn, pos, flips]
      if @turn == @user
        @user_moves++
      @turn = -@turn
      @after_move()

    after_move: ->
      if @board.any_moves @turn
        if @turn == @user
          @message text:  @i18n.t.your_turn
        else
          @worker_move()
      else
        @turn = -@turn
        if @board.any_moves(@turn)
          if @turn == @user
            @message text: @i18n.t.i_pass
          else
            @message text: @i18n.t.you_pass, pass: => @worker_move()
        else
          @gameover = true
          outcome = @board.outcome(@user)
          discs = "#{@board.count(@user)}:#{@board.count(-@user)}"
          if outcome > 0
            @message text: i18n.expand('win', {discs}), back: @back
            gtag 'event', 'win',
              event_category: 'game'
              event_label: @level
              value: outcome
          else if outcome < 0
            @message text: i18n.expand('lose', {discs}), back: @back
            gtag 'event', 'lose',
              event_category: 'game'
              event_label: @level
              value: outcome
          else
            @message text: @i18n.t.draw, back: @back
            gtag 'event', 'draw',
              event_category: 'game'
              event_label: @level
              value: outcome

    worker_move: ->
      @message text: @i18n.t.thinking, spin: true
      t = Date.now()
      worker.onmessage = (e) =>
        worker.onmessage = null
        t = Date.now() - t
        console.log "#{t} msec"

        {move, value} = e.data
        console.log 'Move', pos_to_str(move)
        console.log 'Estimated value', Math.round(100*value)/100 if value?
        setTimeout (=>
          @move move
          @thinking = false
        ), if t < 2000 then 2000 - t else 0
      @thinking = true
      worker.postMessage type:'move', board:@board.dump(), turn:@turn

    undo: ->
      throw new Error 'cannot undo' unless @can_undo
      loop
        [turn, pos, flips] = @undo_stack.pop()
        @board.undo turn, pos, flips
        @$emit 'undo'
        break if turn == @user
      @user_moves--
      if @turn != @user
        @turn = @user
        @message text:  @i18n.t.your_turn

      # BLACK MAGIC TO LET VUE UPDATE
      @board.board.push(0)
      @board.board.pop()

    keypress: (e) ->
      return if e.target.nodeName == 'INPUT'
      @keys.push(e.key)
      @keys = @keys.slice(-2)
      str = @keys.join('')
      try
        move = pos_from_str(str)
      catch
        move = 0

      if (move and @turn==@user and @board.can_move(@user, move))
        @move move

  watch:
    can_undo: -> @set_undo_btn(@can_undo, @undo)

    entered_moves: ->
      moves = pos_array_from_str(@entered_moves)
      @$emit 'reset-moves'
      @board = new Board
      success = false
      turn = BLACK
      for move in moves
        flips = @board.move turn, move
        if flips.length == 0
          success = false
          break
        @$emit 'add-move', pos_to_str(move, turn)
        success = true
        if @board.any_moves(-turn)
          turn = -turn
      if success
        @turn = turn
        @after_move()
      else
        @message text: @i18n.t.invalid_moves, error: true

  components: { Disc }
